import React, { Component } from 'react';
import { Button } from 'reactstrap';

class FileItem extends Component {
    constructor(props) {
        super(props);

        const { file } = this.props;
        const isImageType = file.type.startsWith("image/");
        this.state = {
            isImage: isImageType,
            imageData: "data:image/gif;base64,",
            parsingResult: isImageType ? "n/a" : "Not an image.",
            inProgress: false
        };

        if (isImageType) {
            this.getImageData(file).then((result) => {
                this.setState({
                    imageData: result.imageData
                })
            });
        }
    }

    getImageData(file) {
        var reader = new FileReader();
        return new Promise((resolve, reject) => {
            reader.onerror = () => { reader.abort(); reject(new Error("Error parsing file.")); }
            reader.onloadend = function (e) {
                resolve({
                    imageData: reader.result
                });
            };
            reader.readAsDataURL(file);
        });
    }

    getSizeOf(bytes) {
        if (bytes === 0) { return "0.00 b"; }
        var e = Math.floor(Math.log(bytes) / Math.log(1024));
        return (bytes / Math.pow(1024, e)).toFixed(2) + ' ' + ' kmgtp'.charAt(e) + 'b';
    }

    handleCloseClick(e) {
        e.preventDefault();
        const { file } = this.props;
        this.props.onClose(file);
    }

    handleParseClick(e) {
        e.preventDefault();

        this.setState({
            parsingResult: "Processing...",
            inProgress: true
        });

        const proxyurl = "https://cors-anywhere.herokuapp.com/";
        const url = "https://mnist.cobrain.io/api/ivanx32_github/ivan3";
        let base64String = this.state.imageData;
        base64String = base64String.substring(base64String.indexOf(",") + 1); // to remove 'data:image/gif;base64,' from the image data.

        fetch(proxyurl + url, {
            method: 'post',
            headers: {
                'Accept': 'application/json, text/plain, */*',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "method": "predict_digit_file",
                "params": [`b'${base64String}'`],
                "jsonrpc": "2.0",
                "id": 0
            })
        }).then(res => res.json())
            .then(res => {
                if (res.error) {
                    this.setState({
                        parsingResult: `${res.error.message} [${res.error.code}]`,
                        inProgress: false
                    });
                } else {
                    this.setState({
                        parsingResult: `${res.result}`,
                        inProgress: false
                    });
                }
            })
            .catch(error => {
                this.setState({
                    parsingResult: `Error occurred: ${error.message}`,
                    inProgress: false
                });
            });
    }

    render() {
        const { file } = this.props;
        return (
            <div className="file-item fl">
                <a href="#" className="close-classic" onClick={e => this.handleCloseClick(e)}></a>
                <img src={this.state.imageData} alt="" className={this.state.inProgress ? "grayscale" : ""} />
                <p className="text-info">{file.name} ({this.getSizeOf(file.size)})</p>
                <Button type="submit" className="recognize-button" onClick={(e) => this.handleParseClick(e)} disabled={this.state.inProgress || !this.state.isImage} color="primary">Обработать</Button>
                {this.state.inProgress && (
                    <svg className="spinner" viewBox="0 0 50 50">
                        <circle className="path" cx="25" cy="25" r="20" fill="none" strokeWidth="5"></circle>
                    </svg>
                )}
                <p className="text-secondary">Result: {this.state.parsingResult}</p>
            </div>
        );
    }
}

export default FileItem;